















import Vue from 'vue';
import QuizGroupApi from '@/apis/QuizGroupApi';
import { mapState } from 'vuex';

export default Vue.extend({
  computed: {
    ...mapState({
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      quizGroupName: (state: any) => state.currentQuizGroup.name,
    }),
  },
  methods: {
    /**
     *  v-model update value event
     */
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    updateQuizGroupName(e: any) {
      this.$store.commit('updateQuizGroupName', e.target?.value);
    },
    /**
     *  Update quiz group name. Upload name to backend server.
     */
    changeGroupName() {
      const { uuid, name } = this.$store.state.currentQuizGroup;
      QuizGroupApi.updateName(this.$store.state.currentWorkspace.uuid, uuid, name);
    },
  },
});
