<template>
  <div class="about">
    <button @click="triggerA">
      trigger a
    </button>
    <button @click="triggerB">
      trigger b
    </button>
    <div v-for="(res, index) in result" :key="index">
      {{ res }}
    </div>
  </div>
</template>

<script>
// import comingsoon from '@/components/ComingSoon.vue';
import QuizEventApi from '@/apis/QuizEventApi';
import { QuizEventAction } from '@/apis/models/QuizEventModel';

export default {
  components: {
    // comingsoon,
  },
  data() {
    return {
      result: [],
      monitor: -1,
      workspaceId: '293973d9-7b07-4f47-a312-a23b761912c6',
      quizEventId: '0779bb5b-4c9b-43ea-9af1-a62183d20c64',
    };
  },
  mounted() {
    this.startStateMonitor();
  },
  beforeDestroy() {
    clearInterval(this.monitor);
  },
  methods: {
    triggerA() {
      QuizEventApi.getQuizEvent(this.workspaceId, this.quizEventId).then((result) => {
        console.log(result);
        this.result.push(result);
      });
    },
    triggerB() {
      QuizEventApi.doAction(this.workspaceId, this.quizEventId, QuizEventAction.SEND).then(() => {
        console.log('ok');
        this.result.push('ok');
      }).catch((error) => {
        if (error && error.response) {
          if (error.response.status === 409) {
            this.result.push(error.response.data.description);
          }
        }
      });
    },
    triggerC() {
      console.log(new Date(Date.now() + 60000).toISOString());
      console.log(new Date(Date.now()).toISOString());
      QuizEventApi.update(this.workspaceId, this.quizEventId, { name: '不要刪這個', scheduledTime: new Date(Date.now() + 60000).toISOString() }).then((result) => {
        this.result.push(result);
      });
    },
    startStateMonitor() {
      if (this.monitor !== -1) {
        clearInterval(this.monitor);
      }

      this.monitor = setInterval(async () => {
        const state = await QuizEventApi.getQuizEventState(this.workspaceId, this.quizEventId);
        this.result.push(state);
      }, 3 * 1000);
    },
  },
};
</script>

