







import navbar from '@/components/navbar.vue';
import { Role } from '@/apis/models/RoleModel';
import Vue from 'vue';
import { ProfileModel } from '@/apis/models/AccountModel';

export default Vue.extend({
  components: {
    navbar,
  },
  data() {
    return {
      link: [
        {
          name: '',
          value: '',
          address: '',
        },
      ],
    };
  },
  computed: {
    profile(): ProfileModel {
      return this.$store.state.profile;
    },
  },
  mounted() {
    this.roleProfile();
  },
  methods: {
    roleProfile() {
      if (this.profile.roles.includes(Role.ADMIN)) {
        this.link = [
          {
            name: 'QuizGroups',
            value: '題庫',
            address: 'quizgroups', // 如果在mamger下就不要加斜線(/group會導不到)
          },
          {
            name: 'MemberGroups',
            value: '班級',
            address: 'membergroups', // 如果在mamger下就不要加斜線(/group會導不到)
          },
          {
            name: 'QuizEvents',
            value: '測驗',
            address: 'quizevents', // 如果在mamger下就不要加斜線(/group會導不到)
          },
        ];
      }
      if (this.profile.roles.includes(Role.QUIZ_EDITOR)) {
        this.link = [
          {
            name: 'QuizGroups',
            value: '題庫',
            address: 'quizgroups', // 如果在mamger下就不要加斜線(/group會導不到)
          },
        ];
      }
      if (this.profile.roles.includes(Role.MEMBER_EDITOR)) {
        this.link = [
          {
            name: 'MemberGroups',
            value: '班級',
            address: 'membergroups', // 如果在mamger下就不要加斜線(/group會導不到)
          },
        ];
      }
      if (this.profile.roles.includes(Role.EVENT_EDITOR)) {
        this.link = [
          {
            name: 'QuizEvents',
            value: '測驗',
            address: 'quizevents', // 如果在mamger下就不要加斜線(/group會導不到)
          },
        ];
      }
    },
  },
});
